import {
  AppBar,
  Toolbar,
  Box,
  Button,
  Tooltip,
  Menu,
  MenuItem,
  Typography,
  css,
} from "@mui/material";
import { formatEther } from "ethers/lib/utils";
import { useState } from "react";
import "./app-header.module.css";
import { useEtherBalance, useEthers } from "@usedapp/core";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";
import { BigNumber } from "ethers";

interface IAppHeaderProps {
  etherBalance: BigNumber | undefined;
}

export default function AppHeader({ etherBalance }: IAppHeaderProps) {
  const [isMenuOpened, setIsMenuOpened] = useState<null | HTMLElement>(null);
  const { activateBrowserWallet, account, deactivate } = useEthers();

  return (
    <AppBar color="default">
      <Toolbar variant="dense">
        <Box sx={{ ml: "auto" }}>
          {account ? (
            <Box sx={{ flexGrow: 0 }}>
              <Tooltip title="Меню">
                <Button
                  variant="outlined"
                  onClick={(e) => setIsMenuOpened(e.currentTarget)}
                >
                  {account.slice(0, 5)}...{account.slice(-4)}
                </Button>
              </Tooltip>
              <Menu
                anchorEl={isMenuOpened}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "right",
                }}
                open={Boolean(isMenuOpened)}
                onClose={() => setIsMenuOpened(null)}
                sx={{ mt: 1 }}
              >
                <MenuItem>
                  <Typography>
                    Баланс:{" "}
                    {etherBalance ? (
                      <Typography
                        component="span"
                        sx={{ color: "primary.dark" }}
                      >
                        {parseFloat(formatEther(etherBalance))} ETH
                      </Typography>
                    ) : (
                      <Typography
                        component="span"
                        sx={{ color: "text.secondary" }}
                      >
                        скрыт
                      </Typography>
                    )}
                  </Typography>
                </MenuItem>
                <MenuItem>
                  <Typography>Личный кабинет</Typography>
                </MenuItem>
                <MenuItem
                  onClick={(e) => {
                    deactivate();
                    setIsMenuOpened(null);
                  }}
                >
                  <Typography>Выйти</Typography>
                </MenuItem>
              </Menu>
            </Box>
          ) : (
            // <div className={styles.balanceInfo}>
            //   <span className={styles.balance}>
            //     {etherBalance
            //       ? parseFloat(formatEther(etherBalance)) + " ETH"
            //       : "Баланс скрыт"}
            //   </span>
            //   <span className={styles.accountId}>
            //     {account.slice(0, 5)}...{account.slice(-4)}
            //   </span>
            // </div>
            <Button
              onClick={() => activateBrowserWallet()}
              color="primary"
              variant="outlined"
            >
              Connect
            </Button>
          )}
        </Box>
      </Toolbar>
    </AppBar>
  );
}
